@import "jekyll-theme-minimalist";

a:not([href^="http"]) {
  color: #6b2132;
}

a[href^="http://"], a[href^="https://"]:not([href="https://twitter.com/MarMaSchneider"]) {
  color: #6b2132 !important;
}


.Twitter a {
  color: #d0d0d0;
}

main a {
  color: #6b2132;
}

/* Set link highlighting color to #6b2132 */
a:link,
{
  color: #6b2132;
}

.sidebar a {
  color: #6b2132;
}

.sidebar a:hover {
  color: #6b2132 !important;
}

.img-circle {
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    border-radius: 8px;
    margin-bottom: 10px;
}

.profile-picture {
  height: 85px;
  width: 85px;
  position: relative;
  margin: -10px 0 0 15px;
  float: right;
  border-radius: 50%;
  p & {
      padding: 0 0 0 0;
      margin: 0 0 0 4px;
  }
  @media print, screen and (min-width: 961px) {
      display: none !important;
  }
}

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  transition: opacity 500ms;
  visibility: hidden;
  opacity: 0;
}

.overlay:target {
  visibility: visible;
  opacity: 1;
}

.popup {
  margin: 70px auto;
  padding: 20px;
  background: #fff;
  border-radius: 5px;
  width: 30%;
  position: relative;
  transition: all 1s ease-in-out;
}

.popup .close {
  position: absolute;
  top: 10px;
  right: 10px;
  transition: all 200ms;
  font-size: 30px;
  font-weight: bold;
  text-decoration: none;
  color: #333;
}
.popup .close:hover {
  color: #6b2132;
}

.popup .content {
  padding-right: 20px;
  max-height: 30%;
  overflow: auto;
  font: 15px/1.5 "Noto Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

@media screen and (max-width: 700px){
  .box{
    width: 70%;
  }
  .popup{
    width: 70%;
  }
}

.highlight {
  color: #6b2132; /* replace with your desired hex code */
}
